import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import forest from "../images/forest.jpg";
import slots from "../images/slots.jpg";
import people from "../images/people.jpg";
import farm from "../images/farm.jpg";
import box from "../images/box.png";
import drill from "../images/drill.png";
import delivery from "../images/delivery.png";
import events from "../images/events.png";
import guild from "../images/guild.png";
import talisman from "../images/talisman.png";
import auction from "../images/auction.png";
import kits from "../images/kits.png";
import databases from "../images/databases.jpg";
import ColorThief from "color-thief-browser";
import Color from "../Color";

const Work = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: 100%;
    max-height: 100%;
    border-radius: 16px;
    background: url(${(props) => props.$backgroundImage}) center/cover no-repeat;
    margin-top: 10px;
    margin-bottom: 10px;
`;

const InfoWrapper = styled.div`
    background-color: ${(props) =>
            props.$rgb
                    ? `rgba(${props.$rgb[0]}, ${props.$rgb[1]}, ${props.$rgb[2]}, 0.5)`
                    : "rgba(80,80,80,0.5)"};
    border-radius: 16px;
    backdrop-filter: blur(20px);
    padding: 10px;
    margin: 10px;
    max-width: 60%;
    text-align: center;
`;

const Title = styled.div`
    padding: 10px;
    color: white;
    font-size: 32px;
    font-weight: bold;
    mix-blend-mode: difference;
`;

const Description = styled.div`
    padding-bottom: 10px;
    color: white;
    font-size: 20px;
    mix-blend-mode: difference;
`;

const Video = styled.video`
    width: 50%;
    border-radius: 32px;
    border: 2px solid transparent;
    outline: none;
    transition: border-color 0.3s ease;
    margin-bottom: 10px;

    &:hover {
        border-color: ${Color.border};
        cursor: pointer;
    }
`;

const WorkItem = ({ backgroundImage, title, description, videoSrc }) => {
    const [dominantColor, setDominantColor] = useState([80, 80, 80]);
    const imgRef = useRef(null);

    useEffect(() => {
        if (!imgRef.current) return;

        if (imgRef.current.complete) {
            extractColor();
        } else {
            imgRef.current.onload = extractColor;
            imgRef.current.onerror = () => setDominantColor([80, 80, 80]);
        }

        function extractColor() {
            try {
                const colorThief = new ColorThief();
                const color = colorThief.getColor(imgRef.current);
                setDominantColor(color);
            } catch {
                setDominantColor([80, 80, 80]);
            }
        }
    }, [backgroundImage]);

    return (
        <Work $backgroundImage={backgroundImage}>
            <InfoWrapper $rgb={dominantColor}>
                <Title>{title}</Title>
                <Description>{description}</Description>
            </InfoWrapper>

            <Video src={videoSrc} controls loop playsInline />

            <img
                ref={imgRef}
                src={backgroundImage}
                alt=""
                crossOrigin="anonymous"
                style={{ display: "none" }}
                draggable={false}
            />
        </Work>
    );
};

