class Color {
    static background = '#050505';
    static main = '#080808';
    static mainGradient = '#0d0d0d';
    static second = '#141414';
    static secondHovered = '#191919';
    static inverted = '#232323';
    static border = '#ffffff';
    static toAlpha = (hex, alpha) => {
        const r = parseInt(hex.slice(1, 3), 16)
        const g = parseInt(hex.slice(3, 5), 16)
        const b = parseInt(hex.slice(5, 7), 16)
        return `rgba(${r}, ${g}, ${b}, ${alpha})`
    }
    static light = (hex) => {
        const percent = 0.05;
        const r = parseInt(hex.slice(1, 3), 16);
        const g = parseInt(hex.slice(3, 5), 16);
        const b = parseInt(hex.slice(5, 7), 16);
        const to255 = v => Math.min(255, Math.round(v + (255 - v) * percent));
        const lr = to255(r);
        const lg = to255(g);
        const lb = to255(b);
        return `#${lr.toString(16).padStart(2, '0')}${lg.toString(16).padStart(2, '0')}${lb.toString(16).padStart(2, '0')}`;
    }
}

export default Color;